@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin:0;
  margin-top: 100px;
  font-family: 'Poppins', sans-serif;
  background-color: #F6F7FB;
}

form, input, select, option, textarea {
  font-family: 'Poppins', sans-serif;
}

.MainContainer {
  margin-top: 0px;
}
.Topbar {
    width: 100%;
    padding: 0 48px;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    box-shadow: 0 2px 4px #ccc;
    background-color: white;

    display: flex;
    align-items: center;
    justify-content: space-between;
}



.LeftMenu {
    display: flex;
    align-items: center;
    min-height:64px;
}

.LogoWrapper {
    margin-right: 36px;
    display: flex;
    align-items: center;
}

.LogoWrapper img {
    width: 24px;
    height: 28px;
    margin-right: 8px;
}

.BrandName {
    font-size: 24px;
    letter-spacing: -1px;
    font-weight: 500;
    margin: 0;
    color: rgba(0, 0, 0, 0.8);
}

.MenuItem {
    display: inline-block;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.8);

    padding: 24px 0 16px;

    font-size: 14px;
    font-weight: 500;
    letter-spacing: 0.6px;
    margin: 0 10px;
}

.MenuItemActive {
    color: #20B883;
    border-bottom: 3px solid #20B883;
}


.LogoutBtn {
    position: absolute;
    cursor: pointer;
    right:40px;
    display: inline-block;
    text-decoration: none;
    color: rgba(0,0,0,.8);
    padding: 24px 0 16px;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: .6px;
    margin: 0 10px;
    outline: none;
    border:none;
    background-color:#fff;
}
.LoginForm {
    width: 100%;
    max-width: 500px;
    border-radius: 6px;
    background-color: white;
    margin: 0 auto;

    padding: 32px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;

    box-shadow: 0 0 6px #ccc;
}

.LoginForm h1 {
    margin: 0;
    letter-spacing: 1px;
    font-weight: 500;
    margin-bottom: 24px;
}

.InputField {
    width: 100%;
    padding-top: 16px;
    font-size: 14px;
    box-sizing: border-box;
    border: 0;
    border-bottom: 2px solid #8c8c8c;
    margin-bottom: 16px;
    outline: none;
}

.Button {
    display: inline-block;
    border-radius: 30px;
    padding: 14px 36px;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 1px;
    background-color: #20B883;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 24px;
}
.PageWrapper {
    width: 100%;
    box-sizing: border-box;
    padding: 0 8% 4%;
}

.MainHeading {
    margin: 0;
}

.OrdersWrapper {
    width: 100%;
    display: flex;
    align-items: flex-start;
    padding: 24px 0;
}

.FilterWrapper {
    width: 180px;
    margin-top:  25px;
}

.FilterWrapper h3 {
    margin: 0;
}

.FilterOptions {
    display: flex;
    flex-direction: column;
}

.FilterCheckbox {
    margin: 6px 0;
    cursor: pointer;
}

.FilterCheckbox input {
    margin-right: 12px;
}

.OrderTable {
    width: 100%;
    border-collapse: collapse;
    margin-top: 25px;
}

.OrderTable th, .OrderTable td {
    text-align: left;
    padding: 12px 24px;
    font-size: 14px;
}

.TableRow td {
    background-color: #fff;
    border-bottom: 4px solid #F6F7FB;
}

.PrimaryText {
    color: rgba(0, 0, 0, 0.8);
    font-weight: 500;
}

.SecondaryText {
    color: #8c8c8c;
}

.SearchBoxDiv {
    display: flex;
    justify-content: flex-start;
}

.SearchBox {
    width: 40%;
    padding: 12px 24px;
    font-size: 15px;
    border: 0;
    border-bottom: 2px solid #8c8c8c;
    margin: 0 36px 0 0;
    outline: none;
    margin-right:20px;
}

.ResetBtn {
    display: inline-block;
    border-radius: 30px;
    padding: 14px 36px;
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 1px;
    background-color: #20b883;
    color: #fff;
    border: none;
    outline: none;
    cursor: pointer; 
}
